.themeButton{position:relative;display:inline-flex;align-items:center;justify-content:center;white-space:nowrap;height:2.5rem;width:2.5rem;border-radius:.375rem;border:1px solid gray;background-color:rgba(0,0,0,0);cursor:pointer}.icon{height:1.2rem;width:1.2rem;transition:all .2s ease}.sunIcon{color:#1a1a1a}.moonIcon{position:absolute;color:#f7f2ee}