.contacts{display:flex;flex-wrap:wrap;gap:.75rem;margin-top:1rem}.contacts__item{display:flex;align-items:center;gap:.5rem;padding:.5rem .75rem;border:1px solid var(--border);border-radius:.5rem;background:var(--background);color:var(--foreground);text-decoration:none;font-size:.9rem;font-weight:500;transition:background .2s ease,color .2s ease}.contacts__item svg{flex-shrink:0}.contacts__item:hover{background:var(--foreground);color:var(--background)}